import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, TextField, SelectField } from '../components';

import { CustomerQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateCustomer, CustomerTypes } from '../utils/dataTypes';

const initialValues = {
  id: '',
  name: undefined,
  type: undefined,
  address: undefined,
  address2: undefined,
  city: undefined,
  state: undefined,
  zipCode: undefined,
  phone: undefined,
  fax: undefined,
  email: undefined,
  website: undefined,
};

export default function ViewCustomerForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['customer', pathname.split('/')[3]],
    queryFn: CustomerQuery,
    keepPreviousData: false,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateCustomer>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="name"
                label="Company Name"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="address"
                label="Billing Address Line 1"
                placeholder="Building number and street name"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="phone"
                label="Phone"
                disabled
                formik={formik}
              />
              <TextField fieldName="fax" label="Fax" disabled formik={formik} />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SelectField
                fieldName="type"
                label="Customer Type"
                items={CustomerTypes}
                disabled
                formik={formik}
              />
              <TextField
                fieldName="address2"
                label="Billing Address Line 2"
                placeholder="Apartment#, suite#, box#, etc"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="email"
                label="Email"
                placeholder="Enter Email address"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="website"
                label="Website"
                placeholder="Enter company`s URL"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box
            key="bottomControls"
            sx={{ marginTop: -2, marginBottom: 3, p: 1 }}
          >
            <TextField
              fieldName="city"
              label="City"
              placeholder="Enter city"
              disabled
              formik={formik}
            />
            <TextField
              fieldName="state"
              label="State"
              placeholder="Enter state"
              disabled
              formik={formik}
            />
            <TextField
              fieldName="zipCode"
              label="Zip"
              placeholder="Enter ZIP"
              disabled
              formik={formik}
            />
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
